import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    new_data: {
      type: 'astro',
      page: -1,
      num: 0
    },
    jsonArr: [],
  },
  mutations: {
    getType(state, t) {
      state.new_data.type = t
    },
    getPg(state, p) {
      state.new_data.page = p
    },
    getJson(state, j) {
      console.log(j)
      if (state.new_data.page == 0) {
        state.jsonArr = j
      } else {
        for (var i = 0; i < j.length; i++) {
          state.jsonArr.push(j[i])
        }
      }
    },
    getNum(state, n) {
      state.new_data.num = n
    }
  },
  actions: {

  },
  modules: {
  }
})
