<template>
  <div id="app">
    <header>新闻</header>
    <div class="nav">
      <div class="swiper-container gallery-thumbs">
        <div class="swiper-wrapper">
          <div
            class="swiper-slide"
            v-for="item in arr"
            :key="item.type"
            :style="{color:item.type==type?'red':''}"
            @click="type=item.type"
          >{{item.title}}</div>
        </div>
      </div>
    </div>
    <div style="background:#f5f5f5">
      <home></home>
    </div>
  </div>
</template>

<script>
import home from "./components/Home";
import Swiper from "swiper";
export default {
  components: {
    home
  },
  data() {
    return {
      value: "",
      arr: [
        { type: "astro", title: "其他" },
        { type: "cul", title: "文化" },
        { type: "edu", title: "教育" },
        { type: "house", title: "房产" },
        { type: "sports", title: "体育" },
        { type: "tech", title: "科技" },
        { type: "games", title: "游戏" },
        { type: "finance", title: "财经" },
        { type: "ent", title: "娱乐" },
        { type: "auto", title: "汽车" }
      ],
      type: "astro",
      a: null
    };
  },
  watch: {
    type(a) {
      this.$store.commit("getType", a);
    }
  },
  mounted() {
    new Swiper(".swiper-container", {
      slidesPerView: 6,
      slideToClickedSlide: true
    });
  }
};
</script>
