<template>
  <div>
    <mt-loadmore :top-method="loadTop" @top-status-change="handleTopChange" ref="loadmore">
      <div
        class="home"
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10"
      >
        <div v-for="(item,$index) in $store.state.jsonArr" :key="item.title" @click="index=$index">
          <news>
            <span slot="tit" :style="{color:index==$index?'#ccc':''}">{{item.title}}</span>
            <!-- imgs -->
            <div v-if="item.multi_imgs" slot="div_img">
              <div v-for="(i,$i) in item.multi_imgs" :key="$i">
                <el-image :src="i" style="height:100%;width:100%">
                  <div slot="placeholder" class="image-slot">
                    加载中
                    <span class="dot">...</span>
                  </div>
                </el-image>
                <!-- <img :src="i" alt width="100%" height="100%" /> -->
              </div>
            </div>

            <span slot="span_text" style="padding-right:10px">{{item.category2_chn}}</span>
            <span slot="span_time">{{item.update_time|date}}</span>
          </news>
        </div>
        <div style="text-align:center" v-if="loading">
          <p>加载中...</p>
          <mt-spinner :type="3" style="display:inline-block"></mt-spinner>
        </div>
      </div>
      <div slot="top" class="mint-loadmore-top">
        <span v-show="topStatus !== 'loading'" :class="{ 'rotate': topStatus === 'drop' }">↓</span>
        <span v-show="topStatus === 'loading'">Loading...</span>
      </div>
    </mt-loadmore>
  </div>
</template>

<script>
import news from "../components/news.vue";
export default {
  name: "home",
  components: {
    news
  },
  filters: {
    date(time) {
      let oldDate = new Date(time);
      let newDate = new Date();
      var dayNum = "";
      var getTime = (newDate.getTime() - oldDate.getTime()) / 1000;
      if (getTime < 60 * 5) {
        dayNum = "刚刚";
      } else if (getTime >= 60 * 5 && getTime < 60 * 60) {
        dayNum = parseInt(getTime / 60) + "分钟前";
      } else if (getTime >= 3600 && getTime < 3600 * 24) {
        dayNum = parseInt(getTime / 3600) + "小时前";
      } else if (getTime >= 3600 * 24 && getTime < 3600 * 24 * 30) {
        dayNum = parseInt(getTime / 3600 / 24) + "天前";
      } else if (getTime >= 3600 * 24 * 30 && getTime < 3600 * 24 * 30 * 12) {
        dayNum = parseInt(getTime / 3600 / 24 / 30) + "个月前";
      } else if (time >= 3600 * 24 * 30 * 12) {
        dayNum = parseInt(getTime / 3600 / 24 / 30 / 12) + "年前";
      }
      return dayNum;
    }
  },
  data() {
    return {
      index: "",
      loading: false,
      page: 0,
      topStatus: "",
      num: 0
    };
  },
  watch: {
    "$store.state.new_data": {
      handler(a) {
        this.$http
          .jsonp(
            `https://pacaio.match.qq.com/irs/rcd?cid=56&ext=` +
              a.type +
              `&token=c786875b8e04da17b24ea5e332745e0f&num=10&expIds=20190106A13PFT%7C20190108A04MLS&page=` +
              a.page
          )
          .then(data => {
            this.$store.commit("getJson", data.data.data);
            this.loading = false;
          });
      },
      deep: true
    },
    "$store.state.new_data.type"() {
      this.$store.commit("getJson", "");
      this.$store.commit("getPg", 0);
    }
  },
  methods: {
    loadMore() {
      this.loading = true;
      this.page++;
      this.$store.commit("getPg", this.page);
    },
    loadTop() {
      //  加载更多数据
      this.$refs.loadmore.onTopLoaded();
      this.num++;
      this.page = 0;
      this.$store.commit("getNum", this.num++);
      this.$store.commit("getPg", this.page);
    },
    handleTopChange(status) {
      this.topStatus = status;
    }
  }
};
</script>
